/*NAV BAR*/
.navbar{
	top: 0.5em;
  position: relative;
  right: 2.5em;
  font-size: 1.1em;
}
.navbar ol, ul {
    list-style: none;
}
.navbar li {
    display: inline-block;
/*    padding: 20px 0 20px;
*/    vertical-align: middle;
}
.navbar a:hover, a:focus, a:active {
    color: #8F3933;
    text-decoration: none;
}
.navbar a {
    font-family: 'Spectral', serif;
    text-decoration: none;
    transition: color 0.1s, background-color 0.1s;
	  float: right;
    position: relative;
    display: block;
    padding: 16px 0;
    margin: 0 12px;
    letter-spacing: 1px;
    font-size: 1.1em;
    line-height: 0px;
    font-weight: 800;
    transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    color: #AD463E;
}
.navbar a::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 3px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: #7C5436;
    transform-origin: right top;
    transform: scale(0, 1);
    transition: color 0.1s,transform 0.2s ease-out;
}
.navbar a:active::before {
    background-color: #7C5436;
    position: absolute;
}
.navbar a:hover::before, a:focus::before {
    transform-origin: left top;
    transform: scale(1, 1);
    position: absolute;
}

.activeNavItem a::after {
  border-bottom: 3px solid #7C5436;
  bottom: 3px;
  content: " ";
  left: 0;
  position: absolute;
  right: 0;
  display: inline-block;
}

